                        ><br />
                        {{ gameToView?.away_goal }} ...
                      </v-col>
                    </v-row>
                    <v-divider class="my-2"></v-divider>
                    <v-row>
                      <v-col cols="12">
                        <v-icon
                          color="yellow lighten-1"
