                    Fermer
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-container>
        </v-tab-item>

        <v-tab-item key="tab2">
          <v-container fluid>
            <span class="text-caption"
              >Selectionnez un pays parmis la liste pour voir tous ses
              matchs</span
            >
            <v-row>
              <v-col ccols="12" sm="6" md="4">
                <v-autocomplete
                  color="red"
                  v-model="searchValue"
                  :items="countries"
                  item-text="team"
                  item-value="team"
                  dense
                  hide-no-data
                  filled
                  auto-select-first
                  label="Commencez  taper le nom d'un pays"
                  append-outer-icon="mdi-magnify"
                  no-data-text="Aucune donne"
                  @click:append-outer="getAllGamesByCountry"
