<script>
import {
  GET_CUPS_YEAR,
  GET_MATCH_BY_ROUND_PER_YEAR,
  GET_COUNTRY_MATCHES,
  GET_COUNTRIES_NAME,
} from "@/graphql/queries";

export default {
  name: "ConfrontationView",
  data: () => ({
    loading: false,
    loadingForPays: false,
    cups: [],
    countries: [],
    tab: "",
    gameToView: {},
    dialogDetails: false,
    rounds_match: {},
    containsGamesGames: [],
    home_teams: [],
    away_teams: [],
    allGames: [],
    homeGames: [],
    awayGames: [],
    allCountryGames: [],
    selectedItem: "",
    search: "",
    searchValue: "",
    dateFilter: "",
    headers: [
      {
        text: "Date",
        align: "start",
        sortable: true,
        value: "date",
      },
      {
        text: "Domicile",
        value: "home_team",
        sortable: false,
      },
      { text: "Exterieur", value: "away_team", sortable: false },
      { text: "Score", value: "score", align: "center" },
      { text: "Stade", value: "stadium", sortable: false },
      { text: "Attendance", value: "attendance" },
      { text: "Round", value: "round", sortable: false, align: "center" },
      { text: "Details", value: "infos", sortable: false, align: "center" },
    ],
  }),

  methods: {
    getColor(score) {
      let scores = score.split(" ");
      let scoref;
      if (scores.length > 2) {
        // let score1 = scores[0];
        // let score2 = scores[2];
        scoref = scores[1].split("-");

        //scores = scores.split("");
        if (scoref[0] > scoref[1]) return "orange";
      }
      scoref = score.split("-");
      if (scoref[0] > scoref[1]) return "green";
      return "red";
    },
    getDate(date) {
      return new Date(date).toLocaleDateString("fr-fr", {
        weekday: "long",
        year: "numeric",
        month: "short",
        day: "numeric",
      });
    },

    filterCustom(value, search, item) {
      console.log(value, search, item.awayTeamCountries[0].team);
      return this.value.filter((game) => {
        game.awayTeamCountries.team.toString()((team) =>
          team.team.toString().includes(this.search)
        ) ||
          game.homeTeamCountries.filter((team) =>
            team.team.toString().includes(this.search)
          );
      });
    },

    viewGameDetails(game) {
      this.gameToView = Object.assign({}, game);
      this.dialogDetails = true;
    },
    close() {
      this.dialogDetails = false;
    },
    clearInput() {
      this.searchValue = "";
    },
    async getAllGamesByCountry() {
      this.loadingForPays = true;
      await this.$apollo
        .query({
          query: GET_COUNTRY_MATCHES,
          variables: {
            countriesWhere: {
              team: this.searchValue,
            },
          },
        })
        .then((result) => {
          if (result.data.countries?.length >= 0) {
            let res = result.data.countries[0];
            this.homeGames = res?.gamesHomeTeam;
            this.awayGames = res?.gamesAwayTeam;
            let tempArray = this.homeGames?.concat(this?.awayGames);
            this.allCountryGames = tempArray?.sort(
              (a, b) => new Date(b?.date) - new Date(a?.date)
            );
          }
          this.loadingForPays = false;
        });
    },

    async getCupsYear() {
      this.loading = true;
      await this.$apollo
        .query({
          query: GET_CUPS_YEAR,
        })
        .then((result) => {
          if (result.data.cups?.length >= 0) {
            this.cups = result.data.cups;
            this.selectedItem = result.data?.cups[0].year;
          }
          this.loading = false;
        });
    },
    async getByYear() {
      this.loading = true;
      await this.$apollo
        .query({
          query: GET_MATCH_BY_ROUND_PER_YEAR,
          variables: {
            where: { year: this.selectedItem },
            options: {
              limit: 1,
            },
            containsGamesGamesOptions2: {
              sort: [
                {
                  date: "DESC",
                },
              ],
            },
          },
        })
        .then((result) => {
          if (result.data.cups?.length >= 0) {
            this.rounds_match = result.data.cups[0];
            this.containsGamesGames = this.rounds_match.containsGamesGames;
            this.loading = false;
          }
        });
    },
  },
  apollo: {
    countries: {
      query: GET_COUNTRIES_NAME,
      variables: {
        options: {
          sort: [
            {
              team: "ASC",
            },
          ],
        },
      },
    },
  },
  mounted() {},
  created() {
    this.$watch(
      "$apollo.loading",
      (loading) => {
        this.loading = loading;
      },
      { immediate: true }
    );
    this.getCupsYear().then(() => this.getByYear());
  },
  watch: {
    dialogDetails(val) {
      val || this.close();
    },
  },
  computed: {
    filteredGames() {
      return this.containsGamesGames.filter((game) => {
        return (
          game.awayTeamCountries.filter((team) =>
            team.toString().includes(this.search)
          ) ||
          game.homeTeamCountries.filter((team) =>
            team.toString().includes(this.search)
          )
        );
      });
    },
    filteredAllCountryGames() {
      return this.allCountryGames?.filter((game) => {
        return game.date
          .toString()
          .toLowerCase()
          .includes(this.dateFilter.toLowerCase());
      });
    },
    filteredWorldCups() {
      return this.worldCups?.filter((worldCup) => {
        return worldCup.year.toString().includes(this.yearFilter);
      });
    },
  },
};
</script>
